import React from "react";
import css from "./RulesModal.module.css";

const RulesModal = ({ onClick, onChange }) => (
  <div className={css.modalWrapper}>
    <div className={css.rules}>
      <h3>Приветствую в "лабиринте"</h3>

      <h4>Вот, что тебя ждёт:</h4>

      <ul>
        <li>цель: найти финальную точку</li>
        <li>в лабиринте будет указана точка старта</li>
        <li>двигайся от неё, как показывают стрелки</li>
        <li>сделай свой выбор</li>
      </ul>

      <p>*Ты можешь менять размер поля, скорость игры и количество шагов</p>

      <h3>Удачи!</h3>
      <div className={css.controls}>
        <label>
          Не показывать
          <input type="checkbox" onChange={onChange} id="notOpen" />
        </label>
        <button type="button" onClick={onClick}>
          Понятно
        </button>
      </div>
    </div>
  </div>
);

export default RulesModal;
